.Container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Jokes {
    max-width: 1000px;
    width: 100%;
    margin: 5% 0;
    border: solid 2px black;
    border-radius: 10px;
    margin: 2% 0;
    padding: 0;
    background-color: rgb(255, 214, 127);
}
  
.Jokes p {
    margin: 2%;
    font-size: 1.2rem;
}

.Selection {
    display: flex;
    width:fit-content;
    margin: 1% auto;
}

.Heading h2 {
    color: rgb(70, 70, 70);
    font-size: 1.2rem;
    width: fit-content;
    padding: 0.5%;
    margin: 2% auto 1%;
}

#TheJoke {
    display: none;
}

#show-hide{
    background-color: lightgreen;
    display: none;
}