#Pages {
    display: flex;
    margin: 2% auto;
    /*border: solid 1px white;*/
    display: none;
}

.Pages h2 {
    font-size: 1rem;
}

.movies {
    max-width: 1000px;
    margin: 2% auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.movie {
    width: 250px;
    border: solid 2px black;
    border-radius: 10px;
    margin: 4% 2%;
    padding: 1%;
    background-color: rgb(255, 214, 127);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.movie:hover {
    background-color: rgb(245, 204, 117);
}

.movie img {
    border: solid 2px white;
}