button {
  font-size: 1rem;
}

input[type="search"] {
  font-size: 1rem;
}

select{
  font-size: 1rem;
}

.Container {
  display: flex;
  flex-direction: column;
}

.DefinitionContainer {
  max-width: 1000px;
  margin: 2% auto;
}

.Definition {
  width: 100%;
  border: solid 2px black;
  border-radius: 10px;
  margin: 2% 0;
  padding: 0;
  background-color: rgb(255, 214, 127);
}

.Definition p {
  margin: 2%;
  font-size: 1.2rem;
}

.error {
  text-align: center;
  color: red;
  margin: 4%;
}